import React, { useEffect } from 'react';
import './App.scss';
import chest from "./Assets/Ein/chestv2.svg";
import arm from "./Assets/Ein/arm.svg";
import point from "./Assets/Ein/pointv2.svg";
import leg from "./Assets/Ein/leg.svg";

import ear from "./Assets/Ein/ear.svg";
import topface from "./Assets/Ein/topface.svg";
import topsurprised from "./Assets/Ein/topsurprised.svg";
import jojoface from "./Assets/Ein/jojoface.svg";
import topclosed from "./Assets/Ein/topclosed.svg";
import mouth from "./Assets/Ein/mouth.svg";
import mouthsurprised from "./Assets/Ein/mouthsurprised.svg";
import jojomouth from"./Assets/Ein/jojomouth.svg";
import beard from "./Assets/Ein/beard.svg";
import chin from "./Assets/Ein/chin.svg";

const friends = ["alex", "rinnah", "luis", "omar", "lili"];

class App extends React.Component {
  constructor(props){
    super(props);
    this.state ={
      blink: false,
      wave:false,
      userType:"",
      userName:"",
      hello:true,
      tutorial:false,
      clickedPart:"",
      originalPos:[],
      einOrigin:[],
      einConPos:[],
      clickedHeight:0,
      grabbed:false,
      fact:false,
      factText:"",
      insult:false,
      insultText:"",
      insultCount:0,
      dropped:false,
      aboutme:false
    }
    this.handleEnter = this.handleEnter.bind(this);
    this.handleInput = this.handleInput.bind(this);
    this.handleMouseDown = this.handleMouseDown.bind(this);
    this.handleMouseUp = this.handleMouseUp.bind(this);
    this.handleMouseMove = this.handleMouseMove.bind(this);
    this.calculatePos = this.calculatePos.bind(this);
    this.grabEinHandler = this.grabEinHandler.bind(this);
    this.handleAboutMe = this.handleAboutMe.bind(this);
  }
  componentDidMount(){
    setInterval(()=>{
      setTimeout(()=>{
      this.setState({blink:true});
      setTimeout(()=>{this.setState({blink:false})}, 100)},
      Math.floor(Math.random()*4250))
    },4500);
    setTimeout(()=>{this.setState({wave:true})},1500);
    document.getElementById("inputName").addEventListener("keypress", this.handleEnter);

  }
  componentDidUpdate(prevProps, prevStates){
    if(prevStates.grabbed!==this.state.grabbed || this.state.dropped !== prevStates.dropped){
      if(this.state.grabbed){
        this.grabEinHandler(0);
        let iteration = setInterval(()=> this.grabEinHandler(0), 2000)
        if(prevStates.grabbed!==this.state.grabbed){
          let count = this.state.insultCount>=0&&this.state.insultCount<5? this.state.insultCount+2:this.state.insultCount>=5?5:0;
          fetch("https://insult.mattbas.org/api/en/insult.json").then(response=> response.json()).then(data=>this.setState({fact:false,insult:true, tutorial:false,factText:"",insultText:data.insult, insultCount: count}))
        }
        if(this.state.dropped){
          clearInterval(iteration);
        }
      }

    }
  }
  componentWillUnmount(){
    let ein = document.querySelector(".einContainer");
    ein.removeEventListener("mousedown", this.handleMouseDown);
    ein.removeEventListener("mouseup", this.handleMouseUp);
    ein.removeEventListener("touchstart", this.handleDown);
    ein.removeEventListener("touchend", this.handleUp);
  }

  handleMouseDown(e){
    let ein = document.querySelector(".einContainer");
    let einPart =e.target.getAttribute("class");
    let einPos =ein.getBoundingClientRect();
    if(!einPart.includes("Container") && !einPart.includes("Head")&& !einPart.includes("Part")&&!einPart.includes("Box")){
      ein.style.cursor ="grabbing";
      ein.addEventListener("mousemove", this.handleMouseMove);
      ein.addEventListener("touchmove", this.handleMouseMove);
      ein.addEventListener("mouseup", this.handleMouseUp);
      ein.addEventListener("touchend", this.handleMouseUp);
      let pos = document.querySelector(`.${einPart}`).getBoundingClientRect();
      let einConPos = ein.getBoundingClientRect();
      this.setState({
        clickedPart:einPart,
        originalPos: pos,
        einConPos: [einConPos.width, einConPos.height],
        clickedHeight: pos.height,
        einOrigin:[einPos.top, einPos.right, einPos.bottom, einPos.left],
        fact:false,
        insult:false
      });
    }
  }

  handleMouseMove(e){
    if(!this.state.grabbed){
      if(this.state.clickedPart !== ""){
        let part = document.querySelector(`.${this.state.clickedPart}`);
        let devX = e.type==="mousemove"? e.clientX : e.touches[0].clientX
        let devY = e.type==="mousemove"? e.clientY : e.touches[0].clientY
        let position = this.calculatePos(devX, devY, part);
        if(this.state.clickedPart.includes("Arm")){
          if(this.state.clickedPart.includes("left")){
            part.style.transform =`rotate(-${position.angle}deg) scaleY(${0.5+position.stretch})`;
          }
          else if(this.state.clickedPart.includes("right")){
            part.style.transform =`rotate(${position.angle}deg) scaleY(${0.5+position.stretch})`;
          }
        }
        else if (this.state.clickedPart.includes("Leg")){
          if(this.state.clickedPart.includes("left")){
            let angle = position.angle;
            let angleLeg = angle<=35? angle: angle>=285? angle: angle>=152.5?285:35;
            part.style.transform =`rotate(-${angleLeg}deg) scaleY(${0.5+position.stretch})`;
          }
          else if(this.state.clickedPart.includes("right")){
            let angle = position.angle;
            let angleLeg = angle<=75||angle>=325? angle: angle>=215?325:75;
            part.style.transform =`rotate(${angleLeg}deg) scaleY(${0.5+position.stretch})`;
          }
        }
        else if (this.state.clickedPart.includes("Ear")){
          if(this.state.clickedPart.includes("left")){
            let angle = position.angle;
            let angleEar = angle+80<=450&& angle+80>=270? angle+80: angle+80>=180? 270:450;
            part.style.transform =`rotate(-${angleEar}deg) scaleX(${0.5+position.stretch})`;
          }
          else if(this.state.clickedPart.includes("right")){
            let angle = position.angle;
            let angleEar = angle-80<=90? angle-80: angle-80>=172.5? -90: 90;
            part.style.transform =`rotate(${angleEar}deg) scaleX(${0.5+position.stretch})`;
          }
        }
        else if (this.state.clickedPart.includes("chin")){
          let angle = position.angle;
          let angleChin = angle+330<=400? angle+330: angle+330>=545?330:400;
          part.style.transform =`rotate(-${angleChin}deg)`;
        }
        else if (this.state.clickedPart.includes("Beard")){
          if(this.state.clickedPart.includes("left")){
            let angle = position.angle;
            let angleBeard = angle>=290? angle:angle<=145?0 :290;
            part.style.transform =`rotate(-${angleBeard}deg)`;
          }
          if(this.state.clickedPart.includes("right")){
            let angle = position.angle;
            let angleBeard = angle<=70? angle:angle>=215?0 :70;
            part.style.transform =`rotate(${angleBeard}deg)`;
          }
        }

        if(devY<=this.state.einOrigin[0]+10 || devX>= this.state.einOrigin[1]-10 || devY>= this.state.einOrigin[2]-10 || devX <= this.state.einOrigin[3]+10){
          this.setState({grabbed:true})
          part.animate([{transform:"scaleY(1) rotate(0deg)",easing: 'ease-out'}],{duration:200});
          setTimeout(()=>{part.style.transform="scaleY(1) rotate(0deg)"},200);
        };
      }
    }

    else{
      let startX = this.state.einOrigin[3]+(this.state.einOrigin[1]-this.state.einOrigin[3])/2;
      let startY = this.state.einOrigin[0]+(this.state.einOrigin[2]-this.state.einOrigin[0])/2
      let devX = e.type==="mousemove"? e.clientX : e.touches[0].clientX
      let devY = e.type==="mousemove"? e.clientY : e.touches[0].clientY

      let ein = document.querySelector(".einContainer");
      if(startY <=devY){
        ein.style.transform = `translate(${devX-startX},${startY})`
        document.documentElement.style.setProperty("--shadow", "0.2");
      }
      else{
        ein.style.transform = `translate(${devX-startX}px,${devY-startY}px)`
        document.documentElement.style.setProperty("--shadow", "0");
      }
    }
    
  }

  handleMouseUp(e){
    let ein = document.querySelector(".einContainer");
    ein.removeEventListener("mousemove", this.handleMouseMove);
    ein.removeEventListener("touchmove", this.handleMouseMove);
    ein.removeEventListener("mouseup", this.handleMouseUp);
    ein.removeEventListener("touchend", this.handleUp);
    ein.style.cursor ="grab";
    if(!this.state.grabbed){
      let part = document.querySelector(`.${this.state.clickedPart}`)
      part.animate([{},{transform:"scaleY(0.5) rotate(0deg)",easing: 'ease-out'},{transform:"scaleY(1.4) rotate(0deg)",easing: 'ease-out'},{transform:"scaleY(0.8) rotate(0deg)",easing: 'ease-out'}, {transform:"scaleY(1.2) rotate(0deg)",easing: 'ease-out'}, {transform:"scaleY(0.9) rotate(0deg)",easing: 'ease-out'}, {transform:"scaleY(1) rotate(0deg)",easing: 'ease-out'}], {
        duration:400});
      setTimeout(()=>{part.style.transform="scaleY(1) rotate(0deg)"},400);

      if(this.state.clickedPart.includes("leftArm")){
        let count = this.state.insultCount>0&&this.state.insultCount<6? this.state.insultCount-1:this.state.insultCount>5? 5: 0; 
        fetch("https://uselessfacts.jsph.pl/random.json?language=en").then(response=> response.json()).then(data=>this.setState({fact:true, insult:false, tutorial:false,factText:data.text,insultText:"", insultCount: count}))
      }
      else{
        let count = this.state.insultCount>=0&&this.state.insultCount<5? this.state.insultCount+1:this.state.insultCount>=5?5:0;
        fetch("https://insult.mattbas.org/api/en/insult.json").then(response=> response.json()).then(data=>this.setState({fact:false,insult:true, tutorial:false,factText:"",insultText:data.insult, insultCount: count}))
      }
      this.setState({clickedPart:"", originalPos:[], einOrigin:[],clickedHeight: 0});
    }
    else{
      let devX = e.type ==="mouseup"? e.clientX : e.changedTouches[0].clientX
      let devY = e.type ==="mouseup"? e.clientY : e.changedTouches[0].clientY
      let dropTime = (devY+(this.state.einOrigin[2]-this.state.einOrigin[0]))*250/window.innerHeight;
      let startX = this.state.einOrigin[3]+(this.state.einOrigin[1]-this.state.einOrigin[3])/2;
      let disOrgTime = devX-startX<0? Math.abs(devX-this.state.einOrigin[1])*4000/window.innerWidth/2:Math.abs(devX-this.state.einOrigin[3])*4000/window.innerWidth/2;
      let padding = parseFloat(getComputedStyle(document.documentElement).fontSize);

      //incase of fix change padding size if css changed
      let einHeight =this.state.einConPos[0] -5*padding;
      let einWidth = this.state.einConPos[1] -10*padding;
      
      ein.removeEventListener("mousedown", this.handleMouseDown);
      this.setState({dropped:true})
      ein.animate([{},{transform: `translate(${devX-startX}px,0)`}], dropTime);

      setTimeout(()=>{
        document.documentElement.style.setProperty("--shadow", "0.2");
        ein.style.transform =`translate(${devX-startX}px,0)`;
        ein.animate([{width:`${einWidth}px`, height:`${einHeight}px`},{width:`${einWidth*0.85}px`,height:`${einHeight*0.85}px`},{width:`${einWidth*1.1}px`, height:`${einHeight*1.15}px`},{width:`${einWidth*0.9}px`,height:`${einHeight*0.9}px`},{width:`${einWidth}px`, height:`${einHeight*1.05}px`}, {width:`${einWidth}px`, height:`${einHeight}px`}],400)
      }, dropTime);

      setTimeout(()=>{
        this.setState({grabbed:false})
        this.grabEinHandler(disOrgTime)
        ein.animate([{},{transform: "translate(0,0)"}], disOrgTime)
      }, dropTime+400)

      setTimeout(()=>{
        this.setState({
          dropped:false,
          grabbed:false,
          clickedPart:"", 
          originalPos:[], 
          einOrigin:[],
          clickedHeight: 0})
        ein.style.transform ="translate(0,0)"
        ein.addEventListener("mousedown", this.handleMouseDown);
      }, disOrgTime+dropTime+400)
    }
  }

  calculatePos(x,y,ele){
    let elementPosition = ele.getBoundingClientRect();
    let originalPos = this.state.originalPos;
    let xPos;
    let yPos;
    if(this.state.clickedPart.includes("Arm")){
      if(this.state.clickedPart.includes("left")){
        xPos=originalPos.right;
        yPos=originalPos.top;
      }
      else if(this.state.clickedPart.includes("right")){
        xPos=originalPos.left;
        yPos=originalPos.top;
      }
    }
    else if (this.state.clickedPart.includes("Leg")){
      if(this.state.clickedPart.includes("left")){
        xPos=originalPos.right;
        yPos=originalPos.top;
      }
      else if(this.state.clickedPart.includes("right")){
        xPos=originalPos.left;
        yPos=originalPos.top;
      }
    }
    else if (this.state.clickedPart.includes("Ear")){
      if(this.state.clickedPart.includes("left")){
        xPos=originalPos.left;
        yPos=originalPos.bottom;
      }
      else if(this.state.clickedPart.includes("right")){
        xPos=originalPos.right;
        yPos=originalPos.bottom;
      }
    }


    let stretchDistance = Math.sqrt((x-xPos)**2 + (y-yPos)) / this.state.clickedHeight;
    
    if(this.state.clickedPart.includes("left")){
      let calAngle = (Math.atan2(x-elementPosition.right, y-elementPosition.top) * (180/Math.PI)+360)%360;
      return {angle:calAngle, stretch:stretchDistance}
    }
    else if(this.state.clickedPart.includes("right")){
      let calAngle = (Math.atan2(x-elementPosition.left, y-elementPosition.top) * (180/Math.PI)+360)%360;
      return {angle:calAngle, stretch:stretchDistance}
    }
    else if(this.state.clickedPart.includes("chin")){
      let calAngle = (Math.atan2(x-(elementPosition.left +(elementPosition.left-elementPosition.right)/2), y-elementPosition.top) * (180/Math.PI)+360)%360;
      return {angle:calAngle}
    }
  }

  grabEinHandler(time){
    let leftArm = document.querySelector(".leftArm");
    let rightArm = document.querySelector(".rightArm");
    let leftLeg = document.querySelector(".leftLeg");
    let rightLeg = document.querySelector(".rightLeg");
    let topface = document.querySelector(".top");
    let mouth = document.querySelector(".mouthBox");
    let leftBeard = document.querySelector(".leftBeard");
    let rightBeard =  document.querySelector(".rightBeard");
    let leftEar = document.querySelector(".leftEar");
    let rightEar = document.querySelector(".rightEar");

    if (this.state.grabbed&&!this.state.dropped){
      topface.animate([{transform:"perspective(10rem) rotateX(0deg) rotateY(0deg)"},{transform:"perspective(10rem) rotateX(-10deg) rotateY(20deg)"},{transform:"perspective(10rem) rotateX(-10deg) rotateY(-20deg)"},{transform:"perspective(10rem) rotateX(-10deg) rotateY(20deg)"},{transform:"perspective(10rem) rotateX(-10deg) rotateY(-20deg)"},{transform:"perspective(10rem) rotateX(-10deg) rotateY(20deg)"},{transform:"perspective(10rem) rotateX(-10deg) rotateY(-20deg)"},{transform:"perspective(10rem) rotateX(0deg) rotateY(0deg)"}],2000);

      mouth.animate([{transform:"scaleX(1)"},{transform:"scaleX(0.8)"},{transform:"scaleX(1.2)"},{transform:"scaleX(0.8)"},{transform:"scaleX(1.2)"},{transform:"scaleX(0.8)"},{transform:"scaleX(1.2)"},{transform:"scaleX(1)"}], 2000);

      rightArm.animate([{transform: "rotate(0)"},{transform: "rotate(90deg)"},{transform: "rotate(0)"},{transform: "rotate(90deg)"},{transform: "rotate(0)"},{transform: "rotate(90deg)"},{transform: "rotate(0)"},{transform: "rotate(90deg)"},{transform: "rotate(0)"},{transform: "rotate(0)"},{transform: "rotate(90deg)"},{transform: "rotate(0)"},{transform: "rotate(90deg)"},{transform: "rotate(0)"}],2000);

      leftArm.animate([{transform: "rotate(0)"},{transform: "rotate(90deg)"},{transform: "rotate(0)"},{transform: "rotate(90deg)"},{transform: "rotate(0)"},{transform: "rotate(90deg)"},{transform: "rotate(0)"},{transform: "rotate(90deg)"},{transform: "rotate(0)"},{transform: "rotate(0)"},{transform: "rotate(90deg)"},{transform: "rotate(0)"}],2000);

      leftLeg.animate([{transform: "rotate(0)"},{transform: "rotate(50deg)"},{transform: "rotate(0)"},{transform: "rotate(50deg)"},{transform: "rotate(0)"},{transform: "rotate(50deg)"},{transform: "rotate(0)"},{transform: "rotate(50deg)"},{transform: "rotate(0)"},{transform: "rotate(50deg)"},{transform: "rotate(0)"},{transform: "rotate(50deg)"},{transform: "rotate(0)"}],2000)

      rightLeg.animate([{transform: "rotate(0)"},{transform: "rotate(50deg)"},{transform: "rotate(0)"},{transform: "rotate(50deg)"},{transform: "rotate(0)"},{transform: "rotate(50deg)"},{transform: "rotate(0)"},{transform: "rotate(50deg)"},{transform: "rotate(0)"},{transform: "rotate(50deg)"},{transform: "rotate(0)"},{transform: "rotate(50deg)"},{transform: "rotate(0)"},{transform: "rotate(50deg)"},{transform: "rotate(0)"}],2000)
      
      leftBeard.animate([{transform:"rotate(0deg)"},{transform:"rotate(15deg)"},{transform:"rotate(0deg)"},{transform:"rotate(15deg)"},{transform:"rotate(0deg)"},{transform:"rotate(15deg)"},{transform:"rotate(0deg)"},{transform:"rotate(15deg)"},{transform:"rotate(0deg)"},{transform:"rotate(15deg)"},{transform:"rotate(0deg)"},{transform:"rotate(15deg)"},{transform:"rotate(0deg)"},{transform:"rotate(15deg)"},{transform:"rotate(0deg)"}],2000)

      rightBeard.animate([{transform:"rotate(0deg)"},{transform:"rotate(15deg)"},{transform:"rotate(0deg)"},{transform:"rotate(15deg)"},{transform:"rotate(0deg)"},{transform:"rotate(15deg)"},{transform:"rotate(0deg)"},{transform:"rotate(15deg)"},{transform:"rotate(0deg)"},{transform:"rotate(15deg)"},{transform:"rotate(0deg)"},{transform:"rotate(15deg)"},{transform:"rotate(0deg)"},{transform:"rotate(15deg)"},{transform:"rotate(0deg)"}],2000)


      leftEar.animate([{transform:"scaleX(1) rotate(0)"},{transform:"scaleX(1.2) rotate(10deg)"},{transform:"scaleX(0.8) rotate(0)"},{transform:"scaleX(1.2) rotate(10deg)"},{transform:"scaleX(0.8) rotate(0)"},{transform:"scaleX(1.2) rotate(10deg)"},{transform:"scaleX(0.8) rotate(0)"},{transform:"scaleX(1.2) rotate(10deg)"},{transform:"scaleX(0.8) rotate(0)"},{transform:"scaleX(1.2) rotate(10deg)"},{transform:"scaleX(0.8) rotate(0)"},{transform:"scaleX(1)"}], 2000)
      
      rightEar.animate([{transform:"scaleX(1) rotate(0)"},{transform:"scaleX(0.8) rotate(0deg)"},{transform:"scaleX(1.2) rotate(10deg)"},{transform:"scaleX(0.8) rotate(0)"},{transform:"scaleX(1.2) rotate(10deg)"},{transform:"scaleX(0.8) rotate(0)"},{transform:"scaleX(1.2) rotate(10deg)"},{transform:"scaleX(0.8) rotate(0)"},{transform:"scaleX(1.2) rotate(10deg)"},{transform:"scaleX(0.8) rotate(0)"},{transform:"scaleX(1.2) rotate(10deg)"},{transform:"scaleX(1) rotate(0)"}], 2000)
    }
    else if(this.state.dropped){
      leftArm.animate([{transform:"rotate(0)"}],time/2);
      rightArm.animate([{transform:"rotate(0)"}], time/2);
      leftBeard.animate([{transform:"rotate(0deg)"}],time/2);
      rightBeard.animate([{transform:"rotate(0deg)"}],time/2);
      leftEar.animate([{transform:"scaleX(1) rotate(0)"}],time/2);
      rightEar.animate([{transform:"scaleX(1) rotate(0)"}],time/2);

      leftLeg.animate([{transform: "rotate(0)"},{transform: "rotate(30deg)"},{transform: "rotate(0)"},{transform: "rotate(30deg)"},{transform: "rotate(0)"},{transform: "rotate(30deg)"},{transform: "rotate(0)"},{transform: "rotate(30deg)"},{transform: "rotate(0)"},{transform: "rotate(30deg)"}],time)

      rightLeg.animate([{transform: "rotate(30deg)"},{transform: "rotate(0)"},{transform: "rotate(30deg)"},{transform: "rotate(0)"},{transform: "rotate(30deg)"},{transform: "rotate(0)"},{transform: "rotate(30deg)"},{transform: "rotate(0)"},{transform: "rotate(30deg)"},{transform: "rotate(0)"}],time)
    }
  }

  handleEnter(e){
    if(e.keyCode === 13){
      document.getElementById("inputName").removeEventListener("keypress", this.handleEnter);
      let ein = document.querySelector(".einContainer");
      ein.addEventListener("mousedown", this.handleMouseDown);
      ein.addEventListener("touchstart", this.handleMouseDown);
      this.setState({
        userType:"",
        userName: this.state.userType,
        wave:false,
        hello:false,
        tutorial:true
      })
    }
  }

  handleInput(e){
    this.setState({
      userType: e.target.value
    })
  }
  handleAboutMe(){
    this.setState({
      aboutme:!this.state.aboutme
    })
  }

  render(){
    return (
      <div className ="playgroundContainer">
        <AboutMe aboutme={this.state.aboutme} clickAbout = {this.handleAboutMe}/>
        {this.state.hello? 
        <Intro userType = {this.state.userType} handleInput = {this.handleInput}/>:""}
        {this.state.tutorial?
        <Tutorial userName = {this.state.userName}/>:""}
        {this.state.fact?
        <Fact factText = {this.state.factText}/>:""}
        {this.state.insult?
        <Insult insultText = {this.state.insultText} count={this.state.insultCount} />:""}
        <Ein blink={this.state.blink} wave ={this.state.wave} fact={this.state.fact} grabbed = {this.state.grabbed} insultCount = {this.state.insultCount}/>
      </div>
    );
  }
}

const Ein = (props) =>{
  let einMouth = mouth;
  let baseface = topface;
  let leftArm = props.fact? point: arm;
  if(props.fact){
    let leftArm = document.querySelector(".leftArm");
    leftArm.animate([{easing: 'linear',offset:0.2},{transform: "scaleY(-1) rotate(25deg)"},{transform: "scaleY(-1.3) rotate(25deg)"},{transform: "scaleY(-1) rotate(25deg)"},{transform: "scaleY(-1.3) rotate(25deg)"},{transform: "scaleY(-1) rotate(25deg)"},{transform: "scaleY(-1.3) rotate(25deg)", offset:0.8},{transform: "scaleY(1) rotate(0)"}], {duration:2000})
  }
  if(props.wave){
    let leftArm = document.querySelector(".leftArm");
    leftArm.animate([{easing: 'linear',offset:0.2},{transform: "scaleY(-1) rotate(5deg)"},{transform: "scaleY(-1) rotate(25deg)"},{transform: "scaleY(-1) rotate(5deg)"},{transform: "scaleY(-1) rotate(25deg)"},{transform: "scaleY(-1) rotate(5deg)"},{transform: "scaleY(-1) rotate(25deg)"},{transform: "scaleY(1) rotate(0)"}], {duration:3000})
  }
  if(props.insultCount>0){
    if(props.insultCount ===1 || props.insultCount ===2){
      baseface = topsurprised;
    }
    else if(props.insultCount ===3 || props.insultCount === 4){
      baseface = topsurprised;
      einMouth = mouthsurprised;
    }
    else if (props.insultCount >= 5){
      baseface = jojoface;
      einMouth = jojomouth;
    }
  }

  useEffect(()=>{
    if(props.insultCount>0){
      let mouthAni= document.querySelector(".mouth");
      mouthAni.animate([{easing: 'linear',offset:0.2},{transform: "scaleX(0.8)"},{transform: "scaleX(1)"},{transform: "scaleX(0.8)"},{transform: "scaleX(1)"},{transform: "scaleX(0.8)"},{transform: "scaleX(1)"},{transform: "scaleX(0.8)"},{transform: "scaleX(1) rotate(0)"}], {duration:2000})
    }
  },[props.insultCount])

  let eyes = props.blink? topclosed : baseface;

  return(
    <div className = "einContainer">
      <div className ="headPart">
        <div className ="leftHead">
          <div className = "leftEarBox">
            <img className ="leftEar" draggable="false" src = {ear} alt ="leftEar"/>
          </div>
          <div className ="leftBeardBox">
            <img className ="leftBeard" draggable="false" src ={beard} alt="leftBeard"/>
          </div>
        </div>
        <div className ="middleHead">
          <div className = "topBox">
            <img className = "top" draggable="false" src ={eyes} alt = "top"/>
          </div>
          <div className = "mouthBox" >
            <img className = "mouth" draggable="false" src={einMouth} alt="mouth"/>
          </div>
          <div className = "chinBox">
            <img className = "chin" draggable="false" src ={chin} alt = "chin"/>
          </div>
        </div>
        <div className="rightHead">
          <div className ="rightEarBox">
            <img className ="rightEar" draggable="false" src = {ear} alt ="rightEar"/>
          </div>
          <div className ="rightBeardBox">
            <img className ="rightBeard" draggable="false" src ={beard} alt="rightBeard"/>
          </div>
        </div>
      </div>
      <div className = "bottomPart">
        <div className = "leftArmBox">
          <img className = "leftArm" draggable="false" src ={leftArm} alt ="leftArm"/>
        </div>
        <div className = "middleBottom">
          <div className = "torsoBox">
            <img className = "chest" draggable="false" src = {chest} alt ="chest"/>
          </div>
          <div className = "legsContainer">
            <div className = "leftLegBox">
              <img className = "leftLeg" draggable="false" src = {leg} alt ="leftLeg"/>
            </div>
            <div className = "rightLegBox">
              <img className = "rightLeg" draggable="false" src = {leg} alt ="rightLeg"/>
            </div>
          </div>
        </div>
        <div className = "rightArmBox">
          <img className = "rightArm" draggable="false" src ={arm} alt ="rightArm"/>
        </div>
      </div>
    </div>
  )
}

const Intro = (props) =>{
  return(
    <div className = "introText">
      <h2 className = "name">Hello! My name is Ein</h2>
      <div className = "inputBox">
          <h2>what is yours?</h2>
          <input id ="inputName" type="text" maxLength="14" onChange= {props.handleInput} value={props.userType}/>
      </div>
    </div>
  )
}

const Tutorial = (props) =>{
  let friend = friends.includes(props.userName.toLowerCase());
  let intro = friend? "introSumF":"introSum";
  let guide = friend? "guideF" : "guide";
  let warning = friend? "warningF": "warning";

  return(
    <div className = "tutorialBox">
      {friend?
      <div className = "friendBox">
        <h2>Oh,{props.userName}!?</h2>
        <h2>Have we Met? ...... Well!</h2>
      </div>:
      <h2 className = "normalName">Hi, {props.userName}!</h2>}
      <h3 className = {intro}>Today I will give you a random fact you didn't ask for!</h3>
      <p className ={guide}>Pull my <span>right arm</span> for random fact!</p>
      <p className = {warning}>But! DO NOT under any circumstance, pick me up or pinch me!!!</p>
    </div>
  )
}

const Fact = (props) =>{
  return(
    <div className = "factBox">
      <h2 className = "factIntro">Did you Know?</h2>
      <h2 className = "factText">{props.factText}</h2>
    </div>
  )
}

const Insult = (props)=>{
  let insult;
  if(props.count<=1){
    insult ="OK? wrong part over there!";
  }
  else if(props.count===2){
    insult = "Ha Ha! So Funny!";
  }
  else if (props.count ===3){
    insult = "Alright!? Not Funny";
  }
  else if (props.count ===4){
    insult = "I will get Mad!!!!!!";
  }
  else if (props.count >=5){
    insult = props.insultText;
  }

  return(
    <div className = "InsultBox">
      <h2>{insult}</h2>
    </div>
  )
}

